var qqMap = require("@/libs/qqmap-wx-jssdk.js");
var qqmapsdk;
import api from '@/api/api.js'
export default {
	data() {
		return {
			banners: [{
					src: "/static/banner3.jpg",
					background: "rgb(203, 87, 60)",
				},
				{
					src: "/static/banner2.jpg",
					background: "rgb(205, 215, 218)",
				},
				{
					src: "/static/banner4.jpg",
					background: "rgb(183, 73, 69)",
				}
			],
			swiperCurrent: 0,
			classImg: [{
					src: "/static/c1.png",
					text: '环球美食'
				},
				{
					src: "/static/c2.png",
					text: '个护美妆'
				},
				{
					src: "/static/c3.png",
					text: '营养保健'
				},
				{
					src: "/static/c4.png",
					text: '家居厨卫'
				},
				{
					src: "/static/c5.png",
					text: '速食生鲜'
				}
			],
			goods: [],
			downTime: {},
			isLoad:true
		};
	},
	components:{
		
	},
	onLoad() {
		console.log("开始了")
		qqmapsdk = new qqMap({
			key: '5WRBZ-IJJ6W-J54RP-RKQNR-FFWRJ-WOFFZ'
		});
		let self =this;
		uni.getLocation({
		    type: 'wgs84',
		    success: function (res) {
		        console.log('当前位置的经度：' + res.longitude);
		        console.log('当前位置的纬度：' + res.latitude);
				self.getArea(res.longitude,res.latitude)
		    }
		});
		setInterval(() => {
			self.downTime = self.$formatTime.countDown("2020/12/31 00:00:00")
		}, 1000)
		this.getGoods().then((res) =>{
			console.log(res)
		})
		
	},
	onShow(){
		// this.test()

	},
	methods: {
		
		getArea(jd,wd){
			// 坐标转化为详细地址
			qqmapsdk.reverseGeocoder({
						location:  wd + ',' +jd,
			            success: function (res) {
			                console.log(res.result.address_component.city);
			            },
			            fail: function (res) {
			                console.log(res);
			            },
			        complete: function (res) {
			            console.log(res);
			        }
			    });
		},
		async getGoods() {
			if(!this.isLoad) return
			try{
				this.isLoad = false
				const data = await api.goods({
					method: 'post',
					query: {
						categoryId: 69310,
						nameLike: '',
						page: 1,
						pageSize: 10
					}
				})
				this.isLoad = true
				if (data.code === 0) {
					data.data.forEach(item => {
						this.goods.push(item)
					})
					return data
				} else {
					 console.log(data.msg)
				}
			}catch(err){
				console.log(err)
			}
		},
		onRight() {
			console.log(123)
		},
		changeItem(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;

		},
		goDetail(id){
			uni.navigateTo({
				url:'/pages/detail/detail?id=' + id
			})
		},
		searchKey(e){
			console.log(e)
		}
	},
	onReachBottom(){
		this.getGoods()
	}
}
